export default {
	data() {
		return {
			navdata:['分类1', '分类2', '分类3'],
			navindex:-1
		};
	},
	methods: {
		bian: function(index) {
			this.myindex = index;
		},
		opennew: function(id) {
			uni.navigateTo({
				url: '../' + id + '/' + id
			});
		},
		showAction:function(){
			var that=this
			uni.showActionSheet({
			    itemList: that.navdata,
			    success: function (res) {
					that.navindex=res.tapIndex
			    },
			    fail: function (res) {
			        console.log(res.errMsg);
			    }
			});
		}
	}
};
